import { useEffect, useRef, useState } from "react";
import '../styles/HighlightInput.css'
import { signWithGoogle, logout, registerEvent } from '../firebase';
import { useAuth } from "../context/AuthContext";
import wordsAndColors from "../files/wordsAndColors";
import Terms from './Terms'

function HighlightInput() {
  const [message, setMessage] = useState('')
  const [result, setResult] = useState('');
  const [isLogged, setIsLogged] = useState(false)
  const [loading, setLoading] = useState(false);
  const [isActive, setIsActive] = useState(false);
  const [buttonBlock, setButtonBlock] = useState(false);

  const inputRef = useRef(null);

  const { user } = useAuth();

  const buttonCooldown = () => {
    setButtonBlock(true)
    setTimeout(() => {
      setButtonBlock(false)
    }, 600000);
  }

  const handleHighlight = () => {
    const inputElement = inputRef.current;
    let inputValue = inputElement.innerHTML;
    wordsAndColors.forEach(({ word, color }) => {
      const regex = new RegExp(`\\b${word}(s)?\\b`, "gi");

      inputValue = inputValue.replace(
        regex,
        (match) =>
          `<span style="background-color:${color}">${match}</span>`
      );
    });
    const inputText = inputElement.innerText
    setMessage(inputText)
    registerEvent('review_note')
    setIsActive(true)
    inputElement.innerHTML = inputValue;
    inputElement.focus();
  };


  const tryLogin = async () => {
    try {
      signWithGoogle()
    } catch(error) {
      console.error(error)
    }
  }

  const tryLogout = async () => {
    try {
      await logout()
      setIsLogged(false)
    } catch(error) {
      console.error(error)
    }
  }

  const sendRequest = async () => {
    setLoading(true); 
    registerEvent('send_petition')
    const myPrompt = `Instrucciones: En este prompt se te solicita que ennumeres solo las problemticas especficas de la siguiente clnica en espaol, sin repetir partes de la historia. Por favor, no menciones los diagnsticos, antecedentes personales, nombre del paciente o medicamentos que toma, a menos que sean de extrema relevancia.
    Nota mdica: ${message}
    Procedimiento a seguir:
    1. Lee cuidadosamente la nota mdica proporcionada.
    2. Identifica todas las problemticas mdicas especficas que se mencionan en la nota, sin hacer referencia a los diagnsticos, antecedentes personales, nombre del paciente o medicamentos que toma.
    3. Asegrate de no repetir partes de la historia mdica.
    4. Enumera las problemticas identificadas en orden numrico, utilizando frases breves y claras en espaol.
    5. Asegrate de seguir estrictamente las instrucciones proporcionadas y de no incluir informacin innecesaria o no relevante.
    `
    const prompt = myPrompt

    try {
      // const response = await fetch('http://localhost:3080/api/v1/chat', {
      const response = await fetch('https://my-chatbot-dun.vercel.app/api/v1/chat', {
          method: 'POST',
          headers: {
          'Content-Type': 'application/json'
          },
          body: JSON.stringify({ message: prompt }) // Send the object with the 'message' property
      });
      const data = await response.json();
      setResult(data.choices[0].text);
      registerEvent('perition_success')
      buttonCooldown();
      setLoading(false);
    } catch (error) {
      console.error(error);
      registerEvent('failed_petition')
      setLoading(false);
    }
  }

  useEffect(() => {
    if (user) {
      if (user.displayName) {
        setIsLogged(true)
      }
      else {
        setIsLogged(false)
      }
    } else {
      setIsLogged(false)
    }
  }, [user])

  return (
    <div className="container">
      <div className="HighlightInput">
        <div
          ref={inputRef}
          style={{ whiteSpace: "pre-wrap", display: "inline-block" }}
          contentEditable
        />
        <button type="button" onClick={handleHighlight}>Revisar</button>
      </div>
      {
        isActive? 
          <div className='chatbot-container'>
            <h2 className='chatbot-heading'>Inteligencia Artificial:</h2>
            {
              result.length > 0 && 
              <div id='resultados' className='chatbot-analysis' role='alert'>
                {result}
              </div>
            }
            {
              isLogged?
                <div className="button-log-container">
                  {
                    buttonBlock?
                     <button type='button' className='chatbot-btn btn btn-primary button-blocked'>Analizar</button>
                    :
                     <button
                        type='button'
                        className='chatbot-btn btn btn-primary'
                        onClick={sendRequest}
                      >
                        {loading ? "Analizando..." : "Analizar"}
                      </button>
                  }
                  <p className="advice">Importante: El anlisis generado por IA es solo para fines educativos y no debe reemplazar el juicio de un mdico certificado. La informacin puede estar desactualizada ya que el modelo solo tiene informacin hasta 2021.</p>
                </div>
              :
                <div className="button-log-container">
                  <p className="advice">Para tener acceso al analisis con inteligencia artificial, inicia sesion con google.</p>
                    <button
                      type='button'
                      className='chatbot-btn btn btn-primary'
                      onClick={tryLogin}
                    >
                      Acceder con Google
                    </button>
                </div>

            }            
          </div>
        :
          <p className="advice">Nota: Solo se puede analizar con IA despus de haber revisado la nota clnica.</p>
      } 
      {
        isLogged?
          <div className="button-log-container" >
            <button
              type='button'
              className='chatbot-btn btn btn-primary'
              onClick={tryLogout}
            >
              Cerrar Sesion
            </button>
          </div>
        :
          <div>{isLogged}</div>
      }
      <Terms />
    </div>
  );
}

export default HighlightInput;